import { useIdentity } from "../hooks/identity";
import { useState } from "react";
import s from "../style/postForm.module.sass"

export function PostForm (props) {
    var [id, axios] = useIdentity();
    var activeUser = id.users[id.active];

    var [title, setTitle] = useState("");
    var [text, setText] = useState("");
    title.length

    function change(evt) {
        switch(evt.target.name) {
            case "title":
                return setTitle(evt.target.value);
            case "text":
                return setText(evt.target.value);
        }
    }

    function submit(evt) {
        console.log("Title:", title);
        console.log("Text:", text);
        evt.preventDefault();

        //Submit Form
        if(title.length>=1 && title.length<=150) {
            axios.post(`/post/`, {
                title, text
            })
            .then(function (response) {
                //close drawer
                //show post 
            })
            .catch(function (error) {
                console.log(error);
                //console.log(error.response.data);
            });
        }

    }

    return (
        <>
            <form onSubmit={submit} className={s.postForm}>
                <div className={s.titleCounter} data-invalid={title.length<1 || title.length>150}> 
                    <input 
                        type="text" 
                        className={s.title}
                        value={title}
                        onChange={change}
                        name="title" 
                        placeholder="The post's title" 
                        autoComplete="off"
                    />
                    <p>{title.length}/150</p>
                </div>
                
                <textarea 
                    name="text" 
                    value={text}
                    onChange={change}
                    placeholder="Go ahead, unleash your creative Genius!" 
                    rows="4" 
                    autoComplete="off"
                ></textarea>
            
                <div className={s.postButton}><button 
                    type="submit" 
                    value="Post!"
                    data-known={!Boolean(activeUser)} 
                    
                >Post!</button></div>
                <div className={s.author}> 
                    as
                    <span data-known={Boolean(activeUser)} >
                        {activeUser ? activeUser.username : "<UNKNOWN>"}
                    </span>
                </div>
            
                
            </form>
        </>
    );
}
